@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Serif:ital@1&display=swap);
.relative_space {
  position: relative;
  height: 30vh;
}

.img_category {
  position: absolute;
  bottom: 0;
  right: 0;
  object-fit: cover;
  object-position: center center;
  width: 100%;
  height: 30vh;
  z-index: -1;
}

.text_container {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.63);
  color: rgb(255, 255, 255);
  padding: 10px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  width: 100%;
}

.text_category {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  font-size: small;
}

.text_quantity {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: x-small;
}

.st0 { fill: #ffdc84; }

.Categories {
  height: 100vh;
}

.container_filter {
  padding: 5px;
  background-color: rgb(204, 204, 204);
  font-family: 'PT Serif', serif;
  font-size: small;
  width: 100%;
  margin: auto;
}

select {
  width: 100%;
  font-family: 'PT Serif', serif;
  font-size: small;
  padding: 5px;
  text-align: start;
  color: grey;
}

label {
  width: 100%;
  margin: auto;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box !important;
}

a {
  color: none;
  text-decoration: none; 
}
a:hover {
  color: inherit;
  text-decoration: inherit; 
}
html {
  overflow-x: hidden;
}

body {
  height: 100vh;
  overflow-x: hidden;
  margin: 0;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

